import React from "react";
import axios from "axios";
import Movie from "./Movie"; // Movie 컴포넌트 import
import "./App.css"
import "./Movie.css"

// class 컴포넌트로 변경후 state 정의
class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  };
  // 비동기 실행
  getMovies = async () => {
    const {
      data: {
        data: { movies }
      }
    } = await axios.get("https://yts-proxy.nomadcoders1.now.sh/list_movies.json?sort_by=rating");
    this.setState({ movies, isLoading: false });
  }
  // 생명 주기 함수 : 렌더링 마친 후 실행
  // setTimeout으로 3초 delay 후 setState로 isLoading을 false로 변경
  componentDidMount() {
    this.getMovies();
  }
  render() {
    const {isLoading, movies} = this.state;
    return(
        <section className="container">
          {isLoading
              ? (  // 삼항 연산자 : isLoading이 true일 경우
                  <div className="loader">
                    <span className="loader__text">Loading...</span>
                  </div>
              )
              : (  // 삼항 연산자: isLoading이 false일 경우 Movie 컴포넌트 렌더링
                  <div className="movies">
                    {movies.map(movie => (
                        <Movie
                            key={movie.id}
                            year={movie.year}
                            title={movie.title}
                            summary={movie.summary}
                            poster={movie.medium_cover_image}
                            genres={movie.genres}
                        />
                    ))}
                  </div>
              )}
        </section>
    );
  }
}

export default App;